import { Component, OnInit } from '@angular/core';
import { Model } from 'src/app/api/models/model';
import { RequestService } from 'src/app/api/request.service';
import { ModalController } from '@ionic/angular';
import { DialogService } from 'src/app/api/util/dialog.service';
import { AuthUserService } from 'src/app/services/auth-user.service';
import { PaymentTeamPage } from '../../payment-team/payment-team.page';
import { UtilArrayService } from 'src/app/services/util-array.service';

@Component({
  selector: 'app-teams',
  templateUrl: './teams.page.html',
  styleUrls: ['./teams.page.scss'],
})
export class TeamsPage implements OnInit {

  public oldTeams = [];
  public teams = [];
  public teamService : Model;

  public listSkeleton = new Array(10)
  public firstLoad = true;

  constructor(
    public request : RequestService,
    public modalController: ModalController,
    public dialog : DialogService,
    public auth : AuthUserService,
    public utilArray : UtilArrayService
  ) {
    this.teamService = new Model('Team',request);
   }

  ngOnInit() {

    this.init();
  }

  init(event = null){
    this.teamService.api_function('pageHomeTeams').subscribe(
      response => {
        this.teams = response['data'];
        this.oldTeams = response['data'];
        if(event)
          event.target.complete();
        this.firstLoad = false
      },
      error => {
        if(event)
          event.target.complete();
        this.firstLoad = false;
      }
    )
  }

  close(){
    this.auth.logout();
  }

  async newTeam(){
    const modal = await this.modalController.create({
      component: PaymentTeamPage ,
    });

    modal.onDidDismiss().then(data=>{
      console.log('dimiss team',data);
      if(data.data && data.data['team']){
        const team = data.data['team'];
        //this.teamService.listAddLast(team);
        this.utilArray.listAddLast(this.teams,team);
      }
    })

    return await modal.present();
   
  }

  filterTeams(event){

    let text = event.target.value;
    if(text.length <= 0){
      this.teams = this.oldTeams;
      return;
    }

    this.teams = [... this.oldTeams];

    this.teams = this.teams.filter(team => team.name.toUpperCase().includes(text.toUpperCase()));

  }

  cancelFilter($event){
    this.teams = this.oldTeams;
  }

}
